import React, { useState } from 'react';
import { fetchWeather } from './api/fetchWeather';
import { NavItem, Button } from 'reactstrap';
import './weather.css';
import 'bootstrap/dist/css/bootstrap.min.css';


const App = () => {

    const [query, setQuery] = useState('');
    const [weather, setWeather] = useState({});
    const search = async (e) => {
        if (e.key === 'Enter') {
            const data = await fetchWeather(query)
            setWeather(data);
            setQuery('');
        }
    }

    return (
        <body>
            <div class="Back jumbotron-fluid">
                <div class="container">
                    <div class="container">
                        <h1 className="navibar-brand" href="/">Cek Cuaca</h1>
                        <Button outline color="warning" size="sm" className="home" active href="https://deyanazhari.github.io/cekcuaca/">Home</Button>
                        <Button outline color="warning" size="sm" className="about" active href="https://deyanazhari.github.io/cekcuaca-about/">About</Button>
                        <Button outline color="warning" size="sm" className="cek" active href="https://deyanazhari.github.io/cekcuaca-search/">Cek Cuaca</Button>
                        <NavItem className="navibar-img">
                            <img width="80px" src={'./logo.png'} />
                        </NavItem>
                        <h1 className="contoh"> (Contoh: Jakarta) </h1>

                    </div>
                    <div className="main-container">
                        <input type="text" className="search" placeholder="search..." value={query} onChange={(e) => setQuery(e.target.value)} onKeyPress={search} />
                        {weather.main && (
                            <div className="city">
                                <h2 className="city-name">
                                    <span>{weather.name}</span>
                                    <sup>{weather.sys.country}</sup>
                                </h2>

                                <div className="city-temp">
                                    {Math.round(weather.main.temp)}
                                    <sup>&deg;C</sup>
                                </div>
                                <div className="info">
                                    <img className="city-icon" src={`https://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`} alt={weather.weather[0].description} />
                                    <p>{weather.weather[0].description}</p>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </body>
    );
}
export default App;